import styles from "./video_block.module.css";
import React, { Component } from "react";
import video from "./video.mp4";
import video2 from "./animalvideo.mp4";
import * as Scroll from "react-scroll";
import {
  Link,
  Element,
  Events,
  animateScroll as scroll,
  scrollSpy,
  scroller,
} from "react-scroll";

export default class Video_block extends Component {
  constructor(props) {
    super(props);
    this.video = React.createRef();
    this.state = {
      videoLoaded: false,
      buttonEffect: null,
      media: 2,
      isExpanded: false,
      darkMode: this.props.darkMode,
    };
    this.expandedSwitch = this.expandedSwitch.bind(this);
    this.linkFunc = this.linkFunc.bind(this);
  }

  expandedSwitch() {
    if (this.state.isExpanded == false) {
      this.setState({
        isExpanded: true,
      });
      scroller.scrollTo("section2", {
        duration: 1000,
        delay: 0,
        smooth: "easeInOutQuint",
        offset: 0,
      });
    } else {
      this.setState({
        isExpanded: false,
      });
      scroll.scrollToTop({
        duration: 1000,
        delay: 0,
        smooth: "easeInOutQuint",
      });
    }
  }

  componentWillReceiveProps() {
    setTimeout(() => {
      this.setState({
        darkMode: this.props.darkMode,
      });
      this.state.darkMode
        ? (document.body.style.backgroundColor = "black")
        : (document.body.style.backgroundColor = "white");
      console.log("darkmode is: " + this.state.darkMode);
    }, 5);
  }

  linkFunc() {
    this.props.changeStateFadeOut(true);
    setTimeout(() => {
      window.location.href = this.props.link;
    }, 1000);
  }

  componentDidMount() {
    setInterval(() => {}, 1000);
    const dataIsLoaded = () => {
      this.setState({
        videoLoaded: true,
      });
    };
    const buttonEffect = () => {
      this.setState({ buttonEffect: true });
    };

    const buttonEffectOut = () => {
      this.setState({ buttonEffect: false });
    };

    this.video.current.addEventListener("loadeddata", dataIsLoaded.bind(this));
    this.video.current.addEventListener("mouseover", () => {
      this.setState({ buttonEffect: true });
    });
    this.video.current.addEventListener("mouseout", buttonEffectOut.bind(this));
  }

  render() {
    return (
      <div className={`${styles.contentMain}`}>
        <div
          className={styles.section1}
          style={{ height: `${this.props.height}vh` }}
        >
          <video
            className={styles.video}
            autoPlay
            loop
            muted
            src={this.props.video == "1" ? video : video2}
            style={{
              height: parseInt(this.props.height) + 20 + "vh",
            }}
          ></video>
          <div className={styles.gridcontainer}>
            <div
              className={`item ${
                this.props.tool1 === "javaScript"
                  ? styles.javaScript
                  : this.props.tool1 === "react"
                  ? styles.react
                  : ""
              } `}
            ></div>
            <div
              className={`item ${
                this.props.tool2 === "javaScript"
                  ? styles.javaScript
                  : this.props.tool2 === "react"
                  ? styles.react
                  : ""
              } `}
            ></div>
            <div className={`item ${this.props.tool3}`}></div>
          </div>
          {/* <img
            ref={this.video}
            src="data:image/jpeg;base64,"
          ></img> */}

          <div className={styles.hoverContainer} ref={this.video}></div>
        </div>
        <div className={`${styles.cuttingContainer}`}>
          <div
            className={`${styles.section2} ${
              this.state.isExpanded ? styles.expanded : " "
            } ${this.state.darkMode ? styles.black_background : " "}`}
            onClick={this.expandedSwitch}
            name="section2"
          >
            <div
              className={`${styles.calmContainer} ${
                this.state.darkMode ? styles.black_background : " "
              } ${styles.transition}`}
            >
              <h1
                className={` ${this.state.darkMode ? styles.white_text : " "}`}
              >
                {this.props.headline}
              </h1>
              <p
                className={`${styles.sec2p}  ${
                  this.state.darkMode ? styles.white_text : " "
                }`}
              >
                {`${this.props.text}  ${
                  this.state.isExpanded ? this.props.expandedText : ""
                }`}
              </p>
              <button
                // className={this.state.buttonEffect ? "buttonAttention" : " "}
                className={`${styles.goToAppButton}`}
                onClick={this.linkFunc}
              >
                <p
                  className={`${styles.buttontext}  ${
                    this.state.darkMode ? styles.white_text : " "
                  }`}
                >
                  {`${this.props.buttontext}`}
                </p>
              </button>
              <button
                onClick={this.expandedSwitch}
                className={`${styles.expandButton}  ${
                  this.state.darkMode ? styles.white_text : " "
                }`}
                name="knapp"
                // to="section2"
                smooth={true}
                duration={1000}
              >
                {this.state.isExpanded ? "minimize" : "expand"}
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
